  }
}

@media screen and (max-width: 991.98px) {
  .ftr-group {
    position: relative;
    grid-column: 1/2 !important;
    transition: bacground-color 400ms ease-in-out;
  }
  .ftr-group::after {
    position: absolute;
    content: '';
    top: 0;
    right: -100px;
    left: -100px;
    height: 1px;
    background-color: #e6e6e6;
  }
  .ftr-group--accordion::before {
    position: absolute;
    content: '';
    top: 0;
    right: -100px;
    left: -100px;
    bottom: 0;
    background-color: #F9F7F1;
    opacity: 0;
    transition: 400ms ease-in-out opacity;
  }
  .ftr-group--accordion .fgp-list {
    display: none;
    position: relative;
  }
  .ftr-group--accordion .fgp-hdr {
    cursor: pointer;
    position: relative;
  }
  .ftr-group--accordion.is-open::before {
    opacity: 1;
  }
  .ftr-group--accordion.is-open .fgp-hdr > svg {
    transform: scaleY(-1);
    color: #F55308;
  }
}

